.loader {
  margin: 200px auto;
  border: 10px solid #f3f3f3;
  border-top: 10px solid #5664d2;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
